/*------------------------------------*\
    #VISIBILITY UTILITIES
\*------------------------------------*/

.u-visibility--sr-only {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal !important;

	&:focus {
		background-color: #eee;
		clip: auto !important;
		clip-path: none;
		color: #444;
		display: block;
		font-size: 1em;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
  		z-index: 100000; /* Above WP toolbar. */
	}
}

//
// Generate responsive visibility classes for each breakpoint in $breakpoints
//
@each $bp in map-keys($grid-breakpoints) {
	.u-visibility-hidden-#{$bp}--up {
		@include media-breakpoint-up($bp) {
			display: none;
		}
	}

	.u-visibility-hidden-#{$bp}--down {
		@include media-breakpoint-down($bp) {
			display: none;
		}
	}
}
